.descriptionBlock{
    display: flex;
    padding: 10px;
    font-size: 30px
}

.descriptionBlock img{
    margin-right: 10px
}

.mainPhoto{
    max-width: 200px;
}

.touchBlock{
    position: relative;
}

.spanBlock{
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    color: #B22222;
    padding: 0 17px 0 17.3px;
    transition: .5s;
}

.spanBlock:hover{
    color: #000;
    background-color:wheat;
}

.touchBlock:hover .spanBlock{
    display: block;
}